<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
				<el-breadcrumb-item>店铺费用管理</el-breadcrumb-item>
				<el-breadcrumb-item>指定比例维护</el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.queryObj" class="demo-form-inline">
							<el-row>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label">门店号码：</label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-input v-model="pageData.tableObj.siteNo" :disabled="addDialogDisabled" placeholder="请输入门店号" clearable />
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label">审批状态：</label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-select v-model="pageData.tableObj.oaStatus">
												<el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="12">
									<el-col :span="4"><label class="control-label">提交时间：</label></el-col>
									<el-col :span="20">
										<el-col :span="10">
											<el-date-picker v-model="pageData.tableObj.startTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
										</el-col>
										<el-col :span="2" class="text-center" style="line-height: 40px;">-</el-col>
										<el-col :span="10">
											<el-date-picker v-model="pageData.tableObj.endTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
										</el-col>
									</el-col>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label">科目名称：</label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-select v-model="pageData.tableObj.budgetId">
												<el-option label="全部" value=""></el-option>
												<el-option v-for="item in pageData.budgetList" :label="item.budgetName" :key="item.budgetId" :value="item.budgetId"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="6" class="text-center">
									<el-form-item>
										<el-button type="primary" @click.stop="queryEvt" icon="el-icon-search">查询</el-button>
									</el-form-item>
								</el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-edit-outline" @click.stop="pageData.addDialog.dialogFormVisible = true">新建</el-button>
						</el-col>
						<!--<el-col :span="12" class="text-right">
							<el-button type="primary" plain icon="el-icon-upload">导入</el-button>
						</el-col>-->
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
							<el-table-column sortable prop="id" label="变更单号" min-width="120"></el-table-column>
							<el-table-column sortable prop="siteNo" label="门店号" min-width="110"></el-table-column>
							<el-table-column sortable prop="siteName" label="门店名称" min-width="120"></el-table-column>
							<el-table-column sortable prop="budgetName" label="科目" min-width="90"></el-table-column>
							<el-table-column sortable prop="oaId" label="OA单号" min-width="120"></el-table-column>
							<el-table-column sortable prop="auditorName" label="当前审批人" min-width="130"></el-table-column>
							<el-table-column sortable prop="oaStatusName" label="审批状态" min-width="120"></el-table-column>
							<el-table-column sortable prop="created" label="提交时间" min-width="120"></el-table-column>
							<el-table-column sortable prop="creator" label="提交人" min-width="110"></el-table-column>
							<el-table-column sortable prop="auditOpinion" label="审批意见" min-width="110"></el-table-column>
							<el-table-column fixed="right" label="操作" min-width="100">
								<template slot-scope="scope">
									<el-button @click.stop="detailEvt(scope.$index, scope)" type="text" size="small">查看详情</el-button>
								</template>
							</el-table-column>
						</el-table>
					</div>
					<div class="page">
						<div class="block p15 text-center">
							<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
							:current-page="pageData.tableObj.currentPage" 
							:page-sizes="pageData.tableObj.pageArray"
							:page-size="pageData.tableObj.pageSize"
							layout="total, sizes, prev, pager, next, jumper"
							:total="pageData.tableObj.totalRows">
						</el-pagination>
					</div>
				</div>
			</div>
		</div>
	</div>
	<el-dialog title="比例维护" :visible.sync="pageData.addDialog.dialogFormVisible" width="90%" :before-close="clearEvt">
		<el-switch style="display: block;position: absolute;top: 20px;right: 65px;" v-model="pageData.addDialog.switch" active-color="#13ce66" inactive-color="#ff4949" active-text="展开" inactive-text="收起"></el-switch>
		<el-form class="form-horizontal" v-if="pageData.addDialog.switch">
			<el-row>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">变更单号：</label>
						</el-col>
						<el-col :span="16">
							<el-input :disabled="true" v-model="pageData.addDialog.createRate.id" auto-complete="off" clearable></el-input>	
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">门店：</label>
						</el-col>
						<el-col :span="16">
							<el-input v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.siteNo" :disabled="addDialogDisabled" clearable auto-complete="off"></el-input>
							<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.siteNo" :disabled="pageData.addDialog.isDetail" clearable auto-complete="off"></el-input>
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">科目：</label>
						</el-col>
						<el-col :span="16">
							<el-select v-model="pageData.addDialog.createRate.budgetId" @change="siteNoEvt" :disabled="pageData.addDialog.isDetail" >
								<el-option label="全部" value=""></el-option>
								<el-option v-for="item in pageData.budgetList" :label="item.budgetName" :key="item.budgetId" :value="item.budgetId"></el-option>
							</el-select>	
						</el-col>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">OA单号：</label>
						</el-col>
						<el-col :span="16">
							<el-input v-model="pageData.addDialog.createRate.oaId" :disabled="true" clearable auto-complete="off"></el-input>	
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">状态：</label>
						</el-col>
						<el-col :span="16">
							<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.oaStatusName" :disabled="pageData.addDialog.isDetail" clearable auto-complete="off"></el-input>
							<el-select v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.oaStatus" :disabled="true">
								<el-option v-for="item in pageData.statusList" :key="item.value" :label="item.name" :value="item.value"></el-option>
							</el-select>
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">提交日期：</label>
						</el-col>
						<el-col :span="16">
							<el-date-picker v-model="pageData.addDialog.createRate.created" :disabled="true" value-format="yyyy-MM-dd" type="date" placeholder="选择日期"></el-date-picker>
						</el-col>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<el-table border :data="pageData.addDialog.createRate.oaRateItemList" max-height="490" style="width: 100%;margin-bottom: 10px;">
			<el-table-column sortable prop="budgetName" label="科目名称"></el-table-column>
			<el-table-column sortable prop="partnerTeamName" label="合伙人组"></el-table-column>
			<el-table-column sortable prop="nowRate" label="变更前比例">
				<template slot-scope="scope">
					<span>{{scope.row.nowRate}}%</span>
				</template>
			</el-table-column>
			<el-table-column label="变更后比例">
				<template slot-scope="scope">
					<span v-if="pageData.addDialog.isDetail">{{scope.row.newRate}}%</span>
					<el-input v-if="!pageData.addDialog.isDetail" placeholder="请输入变更后比例" @input="totalNewRateEvt" v-model="scope.row.newRate">
						<template slot="append">%</template>
					</el-input>
				</template>
			</el-table-column>
		</el-table>
		<div class="text-center">
			<el-button v-if="pageData.addDialog.isDetail" type="danger" icon="el-icon-circle-close-outline" @click.stop="clearEvt">关闭</el-button>
			<el-button v-if="!pageData.addDialog.isDetail" type="success" icon="el-icon-upload" @click.stop="addEvt">提交</el-button>
			<el-button v-if="!pageData.addDialog.isDetail" icon="el-icon-circle-close-outline" @click.stop="clearEvt">取消</el-button>
		</div>
	</el-dialog>
</el-main>
</template>

<script type="text/ecmascript-6">
export default {
	data() {
		return {
			addDialogDisabled:false,
			pageData:{
      		statusList:[ // 审批状态
      		{name:"全部",value:"-1"},
      		{name:"提交",value:"0"},
      		{name:"驳回",value:"1"},
      		{name:"已审批",value:"2"}
      		],
      		budgetList:[], // 科目列表
      		tableObj:{
      			oaStatus :"-1", //  OA状态
				mandt :"300", //  大区号
				startTime :"", //  开始时间
				endTime :"", //  结束时间
				siteNo :"", //  门店号
				budgetId :"", //  科目编号
				currentPage: 1,
				pageSize: 10,
      			totalPage: 1, // 多少页
      			totalRows: 1, // 总数
      			pageArray:[10,20,30,40,50],
      			list: []
      		},
      		addDialog:{
      			switch:true,
      			tableHeight:0,
      			isDetail:false,
      			dialogFormVisible:false,
      			createRate:{
					mandt :"", // 大区号
					oaId :"", // OA单号
					siteNo :"", // 门店编号
					siteName :"", // 门店名称
					oaStatus :"0", // OA状态
					auditorId :"", // 审批者编号
					auditorName :"", // 审批者
					delFlag :"", // 标志
					creator :"", // 提交者
					budgetId :"", // 科目编号
					created:this.$api.getDateFunc(true),
					oaRateItemList:[]
					
				},
				formLabelWidth:'120px',
			},
			updateDialog:{
				dialogFormVisible:false,
				form:{},
				formLabelWidth:'120px',
			},
		}
	}
},
created(){
	this.commonInfo()
	let height = document.documentElement.clientHeight
	this.pageData.addDialog.tableHeight = height*0.4
},
methods: {
	initFunc(){
		this.querySubjectType()
	},
	commonInfo(){
		const loading = this.$loading({
			lock: true,
			spinner: 'wm-icon-loading',
			background: 'rgba(0, 0, 0, 0.7)'
		})
		var obj ={}
		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
			method:'post',
			headers:{
				"Content-type":"application/json",
			},
			credentials: "include",
			body:JSON.stringify(obj)
		})
		.then(res=>res.json())
		.then(res=>{
			setTimeout(() => {
				loading.close();
			}, 500)
			if(res.success){
				this.$store.dispatch("setMandt",res.model.mandt)
				this.$store.dispatch("setIndu",res.model.indu)
				this.$store.dispatch("setSiteNo",res.model.siteNo)
				this.pageData.tableObj.mandt = this.$store.getters.mandt
				this.pageData.addDialog.createRate.siteNo = this.$store.getters.siteNo
				this.pageData.tableObj.siteNo = this.$store.getters.siteNo
				if(res.model.siteNo!=""){
					this.addDialogDisabled = true	
				}
				this.initFunc()
			}else{
				this.$message.error(res.message)
			}
		}).catch(err=>{
			setTimeout(() => {
				loading.close();
			}, 500)
			this.$message.error("错误："+err)
		})
	},
	totalNewRateEvt(){
		var list = this.pageData.addDialog.createRate.oaRateItemList,
		totalFee = 0
		list.forEach((el)=>{
			var num = 0
			if(el.newRate){
				num = parseFloat(el.newRate)	
			}
			totalFee += num
			if(totalFee>100){
				this.$message({
					message: '变更后比例总额不能大于100%!',
					type: 'warning'
				})
				el.newRate = ""
				return false
			}
		})
		
	},
	querySubjectType(){
		var obj ={
			mandt : this.pageData.tableObj.mandt,
			subjectTypeId : 1, 
		}
		fetch(this.$api.urls.POST_QUERYSUBJECTTYPE,{
			method:'post',
			headers:{
				"Content-type":"application/json",
			},
			credentials: "include",
			body:JSON.stringify(obj)
		})
		.then(res=>res.json())
		.then(res=>{
			if(res.success){
				this.pageData.budgetList = res.model
				this.queryEvt()
			}else{
				this.$message.error(res.message)
			}
		}).catch(err=>{
			this.$message.error("系统错误："+err)
		})
	},
	siteNoEvt(){
		if(this.pageData.addDialog.createRate.siteNo == ""){
			this.$message({
				message: '请输入门店编号!',
				type: 'warning'
			})
			return false
		}
		const loading = this.$loading({
			lock: true,
          //text: '疯狂加载中…',
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
      })
		let obj = {
      		siteNo:this.pageData.addDialog.createRate.siteNo, // 门店编号
      		budgetId:this.pageData.addDialog.createRate.budgetId, 
      	}
      	fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
      		method:'post',
      		headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
      		body:JSON.stringify(obj)
      	})
      	.then(res=>res.json())
      	.then(res=>{
      		setTimeout(() => {
      			loading.close();
      		}, 500)
      		if(res.success){
	        	// 组数据
	        	const data = res.model
	        	let budgetId = this.pageData.addDialog.createRate.budgetId
	        	let budgetName = ""
	        	for(let i=0;i<this.pageData.budgetList.length;i++){
	        		if(this.pageData.budgetList[i].budgetId == budgetId){
	        			budgetName = this.pageData.budgetList[i].budgetName
	        		}
	        	}
	        	const obj = {
	        		mandt :data.mandt, //  大区号
							oaId :"", //  oa单号
							siteNo :this.pageData.addDialog.createRate.siteNo, //  门店编号
							siteName :data.siteName, //  门店名称
							oaStatus :"0", // OA状态
							auditorId :"", // 审批者编号
							auditorName :"", // 审批者
							delFlag :"", // 标志
							creator :"", // 提交者
							budgetId :budgetId, // 科目编号
							created:this.$api.getDateFunc(true),
							oaRateItemList:[]
						}
						for(let i=0;i<data.partnerTeam.length;i++){
							obj.oaRateItemList.push({
	        			partnerTeamNo :data.partnerTeam[i].partnerTeamNo, // 合伙人组编号
//								nowRate :data.partnerTeam[i].nowRate, //  变更前面积
nowRate:this.$api.accMul(data.partnerTeam[i].nowRate,100),
								newRate :"", //  变更后面积
								partnerTeamName:data.partnerTeam[i].partnerTeamName, // 小店名称
								creator :data.partnerTeam[i].creator, //  提交者
								budgetId :budgetId, // 科目编号
								budgetName:budgetName,
								mandt :data.partnerTeam[i].mandt, //  大区号
							})
						}
						this.pageData.addDialog.createRate = obj
						
					}else{
						this.pageData.addDialog.createRate = {
							mandt :"", // 大区号
							oaId :"", // OA单号
							siteNo :this.$store.getters.siteNo, // 门店编号
							siteName :"", // 门店名称
							oaStatus :"0", // OA状态
							auditorId :"", // 审批者编号
							auditorName :"", // 审批者
							delFlag :"", // 标志
							creator :"", // 提交者
							budgetId :"", // 科目编号
							created:this.$api.getDateFunc(true),
							oaRateItemList:[]
						}
						this.pageData.addDialog.dialogFormVisible = true
						this.pageData.addDialog.isDetail = false
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					this.$message.error("系统错误："+err)
				})
			},
			clearEvt(){
				this.pageData.addDialog.createRate = {
					mandt :"", // 大区号
					oaId :"", // OA单号
					siteNo :this.$store.getters.siteNo, // 门店编号
					siteName :"", // 门店名称
					oaStatus :"0", // OA状态
					auditorId :"", // 审批者编号
					auditorName :"", // 审批者
					delFlag :"", // 标志
					creator :"", // 提交者
					budgetId :"", // 科目编号
					created:this.$api.getDateFunc(true),
					oaRateItemList:[]
				}
				this.pageData.addDialog.dialogFormVisible = false
				this.pageData.addDialog.isDetail = false
			},
			addEvt(){
				if(this.pageData.addDialog.createRate.siteNo == ""){
					this.$message({
						message: '请输入门店编号!',
						type: 'warning'
					})
					return false
				}
				for(var i = 0;i<this.pageData.addDialog.createRate.oaRateItemList.length;i++){
					if(this.pageData.addDialog.createRate.oaRateItemList[i].newRate == ""){
						this.$message({
							message: '请输入变更后比例!',
							type: 'warning'
						})
						return false
					}
				}
				let totalNewRate = 0
				let obj = this.pageData.addDialog.createRate
				obj.oaRateItemList.forEach((item)=>{
					totalNewRate += parseFloat(item.newRate)
				})
				if(totalNewRate!=100){
					this.$message({
						message: `总变更后比例不等于100%，当前${totalNewRate}%!`,
						type: 'warning'
					})
					return false
				}
				obj.oaRateItemList.forEach((item)=>{
					item.nowRate = item.nowRate/100
					item.newRate = item.newRate/100
				})
				const loading = this.$loading({
					lock: true,
			        spinner: 'wm-icon-loading',
			        background: 'rgba(0, 0, 0, 0.7)'
			    })
				fetch(this.$api.urls.POST_RATE_CREATERATE,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
						this.pageData.addDialog.dialogFormVisible = false
						this.$alert('新增比例维护成功', '提示', {
							confirmButtonText: '确定',
							type: 'success',
							center: true,
							callback: action => {
								this.queryEvt()  	
								this.clearEvt()
							}
						})
					}else{
						obj.oaRateItemList.forEach((item)=>{
							item.nowRate = this.$api.accMul(item.nowRate,100)
							item.newRate = this.$api.accMul(item.newRate,100)
						})
						this.pageData.addDialog.createRate = obj
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					obj.oaRateItemList.forEach((item)=>{
						item.nowRate = this.$api.accMul(item.nowRate,100)
						item.newRate = this.$api.accMul(item.newRate,100)
					})
					this.pageData.addDialog.createRate = obj
					this.$message.error("系统错误："+err)
				})
			},
			detailEvt(index,item){
				this.pageData.addDialog.isDetail = true
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				})
				var obj = {
					mandt :this.pageData.tableObj.mandt, 
					mainId :item.row.id, // 主表id
					siteNo:item.row.siteNo
				}
				fetch(this.$api.urls.POST_RATE_QUERYRATEITEM,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
						const data = res.model
						data.forEach((item)=>{
							item.nowRate = this.$api.accMul(item.nowRate,100)
							item.newRate = this.$api.accMul(item.newRate,100)
						})
						var oaStatusName = ""
						for (var i = 0; i <this.pageData.statusList.length; i++) {
							if(this.pageData.statusList[i].value == item.row.oaStatus){
								oaStatusName = this.pageData.statusList[i].name
								break
							}
						}
						this.pageData.addDialog.createRate = {
				    		id :item.row.id, //  大区号
								mandt :item.row.mandt, //  大区号
							oaId :item.row.oaId, //  oa单号
							siteNo :item.row.siteNo, //  门店编号
							siteName :item.row.siteName, //  门店名称
							totalArea :item.row.totalArea, //  总面积
							oaStatus :item.row.oaStatus, //  oa状态
							oaStatusName :oaStatusName, //  oa状态
							auditorId :item.row.auditorId, //  审批者编号
							auditorName :item.row.auditorName, //  审批者
							delFlag :item.row.delFlag, //  删除标记
							created:this.$api.dateFormat("yyyy-MM-dd",item.row.created),
							creator :item.row.creator, //  提交者
							budgetId :item.row.budgetId, // 科目编号
							oaRateItemList: data
						}
						this.pageData.addDialog.dialogFormVisible = true
					}else{
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					this.$message.error("系统错误："+err)
				})
			},
			queryEvt() {
				let obj = {
					oaStatus :this.pageData.tableObj.oaStatus,
					mandt :this.pageData.tableObj.mandt,
					siteNo :this.pageData.tableObj.siteNo, 
					budgetId :this.pageData.tableObj.budgetId, 
					startTime :this.pageData.tableObj.startTime, 
					endTime :this.pageData.tableObj.endTime,
					currentPage :this.pageData.tableObj.currentPage, 
					pageSize :this.pageData.tableObj.pageSize 
				}
				if(this.$api.compareDate(obj.startTime,obj.endTime)){
					this.$message({
						message: '开始时间不能大于结束时间!',
						type: 'warning'
					})
					return false
				}
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				})
				fetch(this.$api.urls.POST_RATE_QUERYRATEMAIN,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
      			this.pageData.tableObj.totalRows = res.model.count // 总数
      			const data =res.model.data
      			for(let i =0;i<data.length;i++){
      				data[i].created = this.$api.dateFormat("yyyy-MM-dd",data[i].created)
      				for(let j=0;j<this.pageData.statusList.length;j++){
      					if(data[i].oaStatus == this.pageData.statusList[j].value){
      						data[i].oaStatusName = this.pageData.statusList[j].name
      						break
      					}
      				}
      				for(let j=0;j<this.pageData.budgetList.length;j++){
      					if(data[i].budgetId == this.pageData.budgetList[j].budgetId){
      						data[i].budgetName = this.pageData.budgetList[j].budgetName
      						break
      					}
      				}
      			}
      			this.pageData.tableObj.list = data
      		}else{
      			this.$message.error(res.message)
      		}
      	}).catch(err=>{
      		setTimeout(() => {
      			loading.close();
      		}, 500)
      		this.$message.error("系统错误："+err)
      	})
      },
      handleSizeChange(val) {
        this.pageData.tableObj.pageSize = val // 单页行数
        this.queryEvt()
    },
    handleCurrentChange(val) {
        this.pageData.tableObj.currentPage = val // 当前页
        this.queryEvt()
    }
}
}
</script>

