<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺科目配置</el-breadcrumb-item>
			  <el-breadcrumb-item>科目维护</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.queryObj" class="demo-form-inline">
							<el-row>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">科目：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.queryObj.query0">
									      <el-option label="全部" value="shanghai"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">类型：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.queryObj.query1">
									      <el-option label="全部" value="shanghai"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">状态：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.queryObj.query2">
									      <el-option label="全部" value="shanghai"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6" class="text-center">
						  		<el-form-item>
								    <el-button type="primary" @click="queryEvt" icon="el-icon-search">查询</el-button>
								  </el-form-item>
							  </el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-edit-outline" @click="pageData.addDialog.dialogFormVisible = true">新建</el-button>
						</el-col>
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column prop="date" sortable label="序号"></el-table-column>
					    <el-table-column prop="name" label="科目编号"></el-table-column>
					    <el-table-column prop="province" label="科目名称"></el-table-column>
					    <el-table-column prop="city" label="科目类型"></el-table-column>
					    <el-table-column prop="address" label="状态"></el-table-column>
					    <el-table-column prop="zip" label="发布人"></el-table-column>
					    <el-table-column prop="zip" label="发布时间"></el-table-column>
					    <el-table-column fixed="right" label="操作" width="120">
					      <template slot-scope="scope">
					        <el-button @click.native.prevent="deleteRow(scope.$index, pageData.tableObj.list)" type="text" size="small">编辑</el-button>
					        <el-button @click.native.prevent="deleteRow(scope.$index, pageData.tableObj.list)" type="text" size="small">禁用</el-button>
					      </template>
					    </el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
					    	:current-page="pageData.tableObj.currentPage"
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>
		<!--新建-->
		<el-dialog title="新增科目维护" :visible.sync="pageData.addDialog.dialogFormVisible" width="40%">
		  <el-form :model="pageData.addDialog.form" class="form-horizontal">
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目编号：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.addDialog.form.name" placeholder="请输入科目编号" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目名称：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.addDialog.form.name" placeholder="输入科目名称" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目类型：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-select v-model="pageData.addDialog.form.region">
			        <el-option label="请选类型" value=""></el-option>
			        <el-option label="区域二" value="beijing"></el-option>
			      </el-select>
		    	</el-col>
		    </el-form-item>
		    <div class="text-left" :style="{marginLeft:pageData.addDialog.formLabelWidth}">
		  		<el-button type="primary" @click="pageData.addDialog.dialogFormVisible = false">保 存</el-button>
		  		<el-button @click="pageData.addDialog.dialogFormVisible = false">取 消</el-button>
		  	</div>
		  </el-form>
		</el-dialog>
		<!--修改-->
		<el-dialog title="修改科目维护" :visible.sync="pageData.updateDialog.dialogFormVisible" width="40%">
		  <el-form :model="pageData.updateDialog.form" class="form-horizontal">
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目编号：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.updateDialog.form.name" placeholder="请输入科目编号" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目名称：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.updateDialog.form.name" placeholder="输入科目名称" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">科目类型：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-select v-model="pageData.updateDialog.form.region">
			        <el-option label="请选类型" value=""></el-option>
			        <el-option label="区域二" value="beijing"></el-option>
			      </el-select>
		    	</el-col>
		    </el-form-item>
		    <el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">状态：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.updateDialog.form.name" disabled placeholder="" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		   	<el-form-item>
		   	 	<el-col :span="8">
		    		<label class="control-label">发布人：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.updateDialog.form.name" disabled placeholder="" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>
		   	<el-form-item>
		    	<el-col :span="8">
		    		<label class="control-label">发布时间：</label>
		    	</el-col>
		    	<el-col :span="10">
		    		<el-input v-model="pageData.updateDialog.form.name" disabled placeholder="" auto-complete="off"></el-input>
		    	</el-col>
		    </el-form-item>

		    <div class="text-left" :style="{marginLeft:pageData.updateDialog.formLabelWidth}">
		  		<el-button type="primary" @click="pageData.updateDialog.dialogFormVisible = false">保 存</el-button>
		  		<el-button @click="pageData.updateDialog.dialogFormVisible = false">取 消</el-button>
		  	</div>
		  </el-form>
		</el-dialog>
	</el-main>
</template>

<script>
export default {
    data() {
      return {
      	pageData:{
      		queryObj:{
      			query0:'',
      			query1:'',
      			query2:'',
      		},
      		tableObj: {
      			currentPage: 1,
      			pageSize: 10,
      			totalPage: 1, // 多少页
      			totalRows: 1, // 总数
      			pageArray:[10,20,30,40,50],
      			list: []
      		},
      		addDialog:{
      			dialogFormVisible:false,
      			form:{},
      			formLabelWidth:'120px',
      		},
      		updateDialog:{
      			dialogFormVisible:false,
      			form:{},
      			formLabelWidth:'120px',
      		},
      	}
      }
    },
    methods: {
      queryEvt() {
        console.log('submit!');
      	fetch('/apis/wumart/partner/create',{
      		method:'get',
      		headers:{
      			"Content-type":"application/json",
      		}
      	}).then(res=>{
      		console.log(res)
      		return res.json()
      	}).then(res=>{
      		console.log(res)
      	}).catch(err=>console.log(err))
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



