// 一级路由
import Home from './../components/home'
import Shopsetting from './../components/shopsetting/index'
import ShopsubjectIndex from './../components/shopsubject/index'
import ShopSubject from './../components/shopsubject/subject'
import Acreage from './../components/shopcost/acreage'
import CostEntry from './../components/shopcost/costEntry'
import SpecifiedProportion from './../components/shopcost/specifiedProportion'
import Log from './../components/log/index'

// 二期模块
import ScoreLeader from './../components/employee/score/leader/index'
import ScoreLeaderDetail from './../components/employee/score/leader/detail'

import ScoreMember from './../components/employee/score/member/index'
import ScoreMemberDetail from './../components/employee/score/member/detail'

import Checkup from './../components/employee/checkup/index'
import CheckupDetail from './../components/employee/checkup/detail'
import CheckupUpload from './../components/employee/checkup/upload'

export const routes = [
	{path:'/',name:'homeLink',component:Home},
	{path:'/shopsetting',name:'shopsettingLink',component:Shopsetting}, // 合并组维护
	{path:'/shopSubject',name:'shopSubjectLink',component:ShopSubject}, // 应用类型查看
	{path:'/shopsubjectIndex',name:'shopsubjectIndexLink',component:ShopsubjectIndex}, // 科目维护
	{path:'/acreage',name:'acreageLink',component:Acreage}, // 面积维护
	{path:'/specifiedProportion',name:'specifiedProportionLink',component:SpecifiedProportion}, // 指定比例维护
	{path:'/costEntry',name:'costEntryLink',component:CostEntry}, // 费用录入
	{path:'/log',name:'logLink',component:Log}, // 操作日志
	{path:'*',redirect:'/'},
	
	// 二期模块
	{path:'/score/leader',name:'scoreLeaderLink',component:ScoreLeader}, // 店长-员工分数评定
	{path:'/score/leader/detail',name:'scoreLeaderDetailLink',component:ScoreLeaderDetail}, // 店长-员工分数评定

	{path:'/score/member',name:'scoreMemberLink',component:ScoreMember}, // 员工-员工分数评定
	{path:'/score/member/detail',name:'scoreMemberDetailLink',component:ScoreMemberDetail}, // 员工-员工分数评定

	{path:'/checkup',name:'checkupLink',component:Checkup}, // 巡检分数导入
	{path:'/checkup/detail',name:'checkupDetailLink',component:CheckupDetail}, // 巡检分数导入-详情
	{path:'/checkup/upload',name:'checkupUploadLink',component:CheckupUpload}, // 巡检分数导入-上传
	
	

]



