<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺费用管理</el-breadcrumb-item>
			  <el-breadcrumb-item>面积维护</el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" class="demo-form-inline">
							<el-row>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">门店号码：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-input v-model="pageData.tableObj.siteNo" :disabled="addDialogDisabled" placeholder="请输入门店号" clearable />
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">审批状态：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.tableObj.oaStatus">
									      <el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="12">
						  		<el-col :span="4"><label class="control-label">提交时间：</label></el-col>
						  		<el-col :span="20">
						  			<el-col :span="10">
						  				<el-date-picker v-model="pageData.tableObj.startTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
						  			</el-col>
						  			<el-col :span="2" class="text-center" style="line-height: 40px;">-</el-col>
						  			<el-col :span="10">
						  				<el-date-picker v-model="pageData.tableObj.endTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder="请选择日期"></el-date-picker>	
						  			</el-col>
						  		</el-col>
							  </el-col>
							</el-row>
							<el-row>
								<el-col :span="24" class="text-center">
						  		<el-form-item>
								    <el-button type="primary" @click.stop="queryEvt" icon="el-icon-search">查询</el-button>
								  </el-form-item>
							  </el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-edit-outline" @click.stop="showDislog">新建</el-button>
						</el-col>
						<!--<el-col :span="12" class="text-right">
							<el-button type="primary" plain icon="el-icon-upload">导入</el-button>
						</el-col>-->
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column sortable prop="id" label="单号" min-width="90"></el-table-column>
					    <el-table-column sortable prop="siteNo" label="门店号" min-width="110"></el-table-column>
					    <el-table-column sortable prop="siteName" label="门店名称" min-width="120"></el-table-column>
					    <el-table-column sortable prop="oaId" label="OA单号" min-width="120"></el-table-column>
					    <el-table-column sortable prop="auditorName" label="当前审批人" min-width="130"></el-table-column>
					    <el-table-column sortable prop="oaStatusName" label="审批状态" min-width="120"></el-table-column>
					    <el-table-column sortable prop="created" label="提交时间" min-width="120"></el-table-column>
					    <el-table-column sortable prop="creator" label="提交人" min-width="110"></el-table-column>
					    <el-table-column sortable prop="auditOpinion" label="审批意见" min-width="110"></el-table-column>
					    <el-table-column fixed="right" label="操作" min-width="100">
					      <template slot-scope="scope">
					        <el-button @click.stop="detailEvt(scope.$index, scope)" type="text" size="small">查看详情</el-button>
					      </template>
					    </el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					    	:current-page="pageData.tableObj.currentPage" 
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>
		<!--新增-->
		<el-dialog title="面积维护" :visible.sync="pageData.addDialog.dialogFormVisible" width="90%" :before-close="clearEvt">
			<el-switch style="display: block;position: absolute;top: 20px;right: 65px;" v-model="pageData.addDialog.switch" active-color="#13ce66" inactive-color="#ff4949" active-text="展开" inactive-text="收起"></el-switch>
		  <el-form class="form-horizontal"  v-if="pageData.addDialog.switch">
		  	<el-row>
		  		<el-col :span="8">
		  			<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">变更单号：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input :disabled="true" v-model="pageData.addDialog.createArea.id" auto-complete="off" clearable></el-input>	
				    	</el-col>
				    </el-form-item>
		  		</el-col>
		  		<el-col :span="8">
		  			<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">门店：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createArea.siteNo" @blur="siteNoEvt" :disabled="addDialogDisabled" auto-complete="off" clearable></el-input>
				    		<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createArea.siteNo" @blur="siteNoEvt" :disabled="pageData.addDialog.isDetail" auto-complete="off" clearable></el-input>
				    	</el-col>
				    </el-form-item>
		  		</el-col>
		  		<el-col :span="8">
		  			<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">门店总面积：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input v-model="pageData.addDialog.createArea.totalArea" :disabled="pageData.addDialog.isDetail" auto-complete="off" clearable></el-input>	
				    	</el-col>
				    </el-form-item>
		  		</el-col>
		  	</el-row>
		    <el-row>
		    	<el-col :span="8">
		    		<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">OA单号：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input v-model="pageData.addDialog.createArea.oaId" :disabled="true" auto-complete="off" clearable></el-input>	
				    	</el-col>
				    </el-form-item>
		    	</el-col>
		    	<el-col :span="8">
		    		<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">*提交日期：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-date-picker v-model="pageData.addDialog.createArea.created" :disabled="true" value-format="yyyy-MM-dd" type="date" clearable placeholder="选择日期"></el-date-picker>
				    	</el-col>
				    </el-form-item>
		    	</el-col>
		    	<el-col :span="8">
		    		<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">状态：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createArea.oaStatusName" :disabled="pageData.addDialog.isDetail" auto-complete="off" clearable></el-input>
				    		<el-select v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createArea.oaStatus" :disabled="true" clearable>
						        <el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
						    </el-select>		
				    	</el-col>
				    </el-form-item>
		    	</el-col>
		    </el-row>
		  </el-form>
		  <el-table border :data="pageData.addDialog.createArea.oaAreaItemList" :max-height="pageData.addDialog.tableHeight" style="width: 100%;margin-bottom: 10px;">
		    <el-table-column sortable prop="partnerTeamName" label="合伙人组"></el-table-column>
		    <el-table-column sortable prop="nowArea" label="变更前面积"></el-table-column>
	      <el-table-column label="变更后面积">
		      <template slot-scope="scope">
		      	<el-input :disabled="pageData.addDialog.isDetail" v-model="scope.row.newArea"></el-input>
		      </template>
		    </el-table-column>
		  </el-table>
	    <div class="text-center">
	    	<el-button v-if="pageData.addDialog.isDetail" type="danger" icon="el-icon-circle-close-outline" @click.stop="clearEvt">关闭</el-button>
	    	<el-button v-if="!pageData.addDialog.isDetail" type="success" icon="el-icon-upload" @click.stop="addEvt">提交</el-button>
	    	<el-button v-if="!pageData.addDialog.isDetail" icon="el-icon-circle-close-outline" @click.stop="clearEvt">取消</el-button>
	    </div>
		</el-dialog>
	
	</el-main>
</template>

<script>
export default {
    data() {
      return {
      	addDialogDisabled:false,
      	pageData:{
      		statusList:[ // 审批状态
      			{name:"全部",value:"-1"},
	      		{name:"提交",value:"0"},
	      		{name:"驳回",value:"1"},
	      		{name:"已审批",value:"2"}
      		],
      		tableObj:{
      			siteNo:"", // 门店编号
      			oaStatus :"-1", //  oa审批状态
				mandt :"", //大区号
				startTime :"", //  开始时间
				endTime :"", //  结束时间
      			currentPage: 1,
      			pageSize: 10,
      			totalPage: 1, // 多少页
      			totalRows: 1, // 总数
      			pageArray:[10,20,30,40,50],
      			list: []
      		},
      		addDialog:{
      			switch:true,
      			tableHeight:0,
      			isDetail:false,
      			dialogFormVisible:false,
      			createArea:{
      				mandt :"", //  大区号
					oaId :"", //  oa单号
					siteNo :"", //  门店编号
					siteName :"", //  门店名称
					totalArea :"", //  总面积
					oaStatus :"0", //  oa状态
					auditorId :"", //  审批者编号
					auditorName :"", //  审批者
					delFlag :"", //  删除标记
					creator :"", //  提交者
					created:this.$api.getDateFunc(true),
					oaAreaItemList:[]
      			},
      			formLabelWidth:'120px',
      		},
      		updateDialog:{
      			dialogFormVisible:false,
      			form:{},
      			formLabelWidth:'120px',
      		}
      	}
      }
    },
    created(){
			this.commonInfo()
			let height = document.documentElement.clientHeight
			this.pageData.addDialog.tableHeight = height*0.4
    },
    methods: {
    	initFunc(){
    		this.queryEvt()
    	},
    	commonInfo(){
				const loading = this.$loading({
	        lock: true,
	        spinner: 'wm-icon-loading',
	        background: 'rgba(0, 0, 0, 0.7)'
	      })
				var obj ={}
	  		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
	      	method:'post',
	      	headers:{
	    			"Content-type":"application/json",
	    		},
	    		credentials: "include",
	      	body:JSON.stringify(obj)
	      })
	      .then(res=>res.json())
	      .then(res=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	if(res.success){
	      		this.$store.dispatch("setMandt",res.model.mandt)
	      		this.$store.dispatch("setIndu",res.model.indu)
	      		this.$store.dispatch("setSiteNo",res.model.siteNo)
	      		this.pageData.tableObj.mandt = this.$store.getters.mandt
	      		this.pageData.addDialog.createArea.siteNo = this.$store.getters.siteNo
	      		this.pageData.tableObj.siteNo = this.$store.getters.siteNo
	      		if(res.model.siteNo!=""){
	      			this.addDialogDisabled = true	
	      		}
	      		this.initFunc()	
	      	}else{
	      		this.$message.error(res.message)
	      	}
	      }).catch(err=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	this.$message.error("错误："+err)
	      })
		
			},
    	clearEvt(){
    		this.pageData.addDialog.createArea = {
					mandt :"", //  大区号
					oaId :"", //  oa单号
					siteNo :this.$store.getters.siteNo, //  门店编号
					siteName :"", //  门店名称
					totalArea :"", //  总面积
					oaStatus :"0", //  oa状态
					auditorId :"", //  审批者编号
					auditorName :"", //  审批者
					delFlag :"", //  删除标记
					creator :"", //  提交者
					created:this.$api.getDateFunc(true),
					oaAreaItemList:[]
				}
    		this.pageData.addDialog.dialogFormVisible = false
    		this.pageData.addDialog.isDetail = false;
    	},
    	showDislog(){
    		
    		if(this.pageData.addDialog.createArea.siteNo!=""){
    			this.siteNoEvt()
    		}else{
    			this.pageData.addDialog.dialogFormVisible = true	
    		}
    	},
    	siteNoEvt(){
    		if(this.pageData.addDialog.createArea.siteNo == ""){
				this.$message({
		          message: '请输入门店编号!',
		          type: 'warning'
		        })
		        return false
			}
				const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
    		let obj = {
      		siteNo:this.pageData.addDialog.createArea.siteNo, // 门店编号
      	}
        fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	// 组数据
	        	const data = res.model
	        	const obj = {
	        		mandt :data.mandt, //  大区号
							oaId :"", //  oa单号
							siteNo :this.pageData.addDialog.createArea.siteNo, //  门店编号
							siteName :data.siteName, //  门店名称
							totalArea :data.totalArea, //  总面积
							oaStatus :"0", //  oa状态
							auditorId :"", //  审批者编号
							auditorName :"", //  审批者
							delFlag :"", //  删除标记
							creator :"", //  提交者
							created:this.$api.getDateFunc(true),
							oaAreaItemList:[]
	        	}
	        	for(let i=0;i<data.partnerTeam.length;i++){
	        		obj.oaAreaItemList.push({
								mainId :"", //  主表id
								partnerTeamNo :data.partnerTeam[i].partnerTeamNo, //  合伙人编号
								nowArea :data.partnerTeam[i].nowArea, //  变更前面积
								newArea :"", //  变更后面积
								partnerTeamName:data.partnerTeam[i].partnerTeamName, // 小店名称
								creator :data.partnerTeam[i].creator, //  提交者
								created :data.partnerTeam[i].created, //  提交时间
								modified :data.partnerTeam[i].modified, // 
								mandt :data.partnerTeam[i].mandt, //  大区号
							})
	        	}
	        	this.pageData.addDialog.createArea = obj
	        	this.pageData.addDialog.dialogFormVisible = true
	        }else{
	        	this.pageData.addDialog.createArea = {
							mandt :"", //  大区号
							oaId :"", //  oa单号
							siteNo :this.$store.getters.siteNo, //  门店编号
							siteName :"", //  门店名称
							totalArea :"", //  总面积
							oaStatus :"0", //  oa状态
							auditorId :"", //  审批者编号
							auditorName :"", //  审批者
							delFlag :"", //  删除标记
							creator :"", //  提交者
							created:this.$api.getDateFunc(true),
							oaAreaItemList:[]
						}
		    		this.pageData.addDialog.dialogFormVisible = true
		    		this.pageData.addDialog.isDetail = false;
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
    	},
      addEvt(){
      	if(this.pageData.addDialog.createArea.siteNo == ""){
					this.$message({
	          message: '请输入门店编号!',
	          type: 'warning'
	        })
	        return false
				}
      	for(var i = 0;i<this.pageData.addDialog.createArea.oaAreaItemList.length;i++){
					if(this.pageData.addDialog.createArea.oaAreaItemList[i].newArea == ""){
						this.$message({
		          message: '请输入变更后面积!',
		          type: 'warning'
		        })
		        return false
					}
				}
      	const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
      	let obj = this.pageData.addDialog.createArea
        fetch(this.$api.urls.POST_AREA_CREATEAREA,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	this.pageData.addDialog.dialogFormVisible = false
	        	this.$alert('新增面积维护成功', '提示', {
		          confirmButtonText: '确定',
		          type: 'success',
		          center: true,
		          callback: action => {
				        this.queryEvt()  	
				        this.clearEvt()
		          }
		        })
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
      },
      detailEvt(index,item){
      	this.pageData.addDialog.isDetail = true;
      	const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
      	var obj = {
      		mandt :this.pageData.tableObj.mandt, 
					mainId :item.row.id, // 主表id
					siteNo:item.row.siteNo
      	}
      	fetch(this.$api.urls.POST_AREA_QUERYAREAITEM,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	var oaStatusName = ""
	        	for (var i = 0; i <this.pageData.statusList.length; i++) {
	        		if(this.pageData.statusList[i].value == item.row.oaStatus){
	        			oaStatusName = this.pageData.statusList[i].name
	        			break
	        		}
	        	}   	
	        	this.pageData.addDialog.createArea = {
	        		id:item.row.id,
      				mandt :item.row.mandt, //  大区号
					oaId :item.row.oaId, //  oa单号
					siteNo :item.row.siteNo, //  门店编号
					siteName :item.row.siteName, //  门店名称
					totalArea :item.row.totalArea, //  总面积
					oaStatus :item.row.oaStatus, //  oa状态
					oaStatusName :oaStatusName, //  oa状态
					auditorId :item.row.auditorId, //  审批者编号
					auditorName :item.row.auditorName, //  审批者
					delFlag :item.row.delFlag, //  删除标记
					creator :item.row.creator, //  提交者
					created:item.row.created,
					oaAreaItemList: res.model
      			}
	        	this.pageData.addDialog.dialogFormVisible = true
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
      },
      queryEvt() {
      	let obj = {
      		siteNo :this.pageData.tableObj.siteNo, //  门店编号 无
					oaStatus :this.pageData.tableObj.oaStatus, //  小店-类型 无
					startTime :this.pageData.tableObj.startTime, //  单页行数
					endTime :this.pageData.tableObj.endTime, //  当前页吗
					mandt :this.pageData.tableObj.mandt, // 大区号
					currentPage :this.pageData.tableObj.currentPage, 
					pageSize :this.pageData.tableObj.pageSize 
      	}
      	if(this.$api.compareDate(obj.startTime,obj.endTime)){
      		this.$message({
	          message: '开始时间不能大于结束时间!',
	          type: 'warning'
	        })
	        return false
      	}
      	const loading = this.$loading({
          lock: true,
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
        fetch(this.$api.urls.POST_AREA_QUERYAREAMAIN,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
      			this.pageData.tableObj.totalRows = res.model.count // 总数
      			const data =res.model.data
	        	for(let i =0;i<data.length;i++){
	        		data[i].created = this.$api.dateFormat("yyyy-MM-dd",data[i].created)
	        		for(let j=0;j<this.pageData.statusList.length;j++){
	        			if(data[i].oaStatus == this.pageData.statusList[j].value){
	        				data[i].oaStatusName = this.pageData.statusList[j].name
	        				break
	        			}
	        		}
	        	}
	        	this.pageData.tableObj.list = data
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
      },
      handleSizeChange(val) {
        this.pageData.tableObj.pageSize = val // 单页行数
        this.queryEvt()
      },
      handleCurrentChange(val) {
        this.pageData.tableObj.currentPage = val // 当前页
        this.queryEvt()
      }
    }
  }
</script>

