<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i>首页</el-breadcrumb-item>
			  <el-breadcrumb-item>店铺配置</el-breadcrumb-item>
			  <el-breadcrumb-item>事业组维护</el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.tableObj" class="demo-form-inline">
							<el-row>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">门店号码：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-input v-model="pageData.tableObj.siteNo" :disabled="addDialogDisabled" placeholder="请输入门店号" clearable />
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label">状态：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.tableObj.status">
									      <el-option v-for="item in pageData.statusList" :key="item.value" :label="item.name" :value="item.value"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  			<el-col :span="8"><label class="control-label">小店类型：</label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.tableObj.shopType">
									      <el-option label="全部" value=""></el-option>
									      <el-option v-for="item in pageData.shopTypeList" :key="item.shopType" :label="item.induName+item.shopType" :value="item.shopType"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6" class="text-center">
						  		<el-form-item>
								    <el-button type="primary" @click.stop="queryEvt" icon="el-icon-search">查询</el-button>
								  </el-form-item>
							  </el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-edit-outline" @click.stop="pageData.addDialog.dialogFormVisible = true">新建</el-button>
						</el-col>
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column sortable prop="id" label="序号" min-width="90"></el-table-column>
					    <el-table-column sortable prop="siteNo" label="门店" min-width="90"></el-table-column>
					    <el-table-column sortable prop="shopNo" label="事业组编号" min-width="130"></el-table-column>
					    <el-table-column sortable prop="shopName" label="事业组名称" min-width="130"></el-table-column>
					    <el-table-column sortable prop="chargeName" label="事业组负责人" min-width="150"></el-table-column>
					    <el-table-column sortable prop="shopType" label="架构类型" min-width="120">
					    	<template slot-scope="scope">
					        <span>{{scope.row.induName + scope.row.shopType}}</span>
					      </template>
					    </el-table-column>
					    <el-table-column sortable prop="statusName" label="状态" min-width="90"></el-table-column>
					    <el-table-column sortable prop="creator" label="发布人" min-width="110"></el-table-column>
					    <el-table-column sortable prop="created" label="发布时间" min-width="120"></el-table-column>
					    <el-table-column fixed="right" label="操作" min-width="100">
					      <template slot-scope="scope">
					        <el-button @click.stop.native.prevent="showUpdate(scope.$index, scope)" type="text" size="small">编辑</el-button>
					        <el-button @click.stop.native.prevent="statusEvt(scope.$index, scope)" type="text" size="small">{{scope.row.status==1?"禁用":"启用"}}</el-button>
					      </template>
					    </el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					    	:current-page="pageData.tableObj.currentPage" 
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>
		<!--新增-->
		<el-dialog title="事业组维护" :visible.sync="pageData.addDialog.dialogFormVisible" width="50%" :before-close="clearEvt">
		  <el-form class="form-horizontal">
		  	<el-row>
		  		<el-col :span="12">
		  			<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">门店号：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-input v-if="!pageData.addDialog.isUpdate" v-model="pageData.addDialog.create.siteNo" :disabled="addDialogDisabled" auto-complete="off"></el-input>
				    		<el-input v-if="pageData.addDialog.isUpdate" v-model="pageData.addDialog.create.siteNo" :disabled="pageData.addDialog.isUpdate" auto-complete="off"></el-input>	
				    	</el-col>
				    </el-form-item>
		  		</el-col>
		  		<el-col :span="12">
		  			<el-form-item>
				    	<el-col :span="8">
				    		<label class="control-label">小店类型：</label>
				    	</el-col>
				    	<el-col :span="16">
				    		<el-select v-model="pageData.addDialog.create.shopType" @change="siteNoEvt" :disabled="pageData.addDialog.isUpdate">
				    			   <el-option label="全部" value=""></el-option>
						      <el-option v-for="item in pageData.shopTypeList" :key="item.shopType" :label="item.induName+item.shopType" :value="item.shopType"></el-option>
						    </el-select>
				    	</el-col>
				    </el-form-item>
		  		</el-col>
		  	</el-row>
		  </el-form>
	  	<el-table border :data="pageData.addDialog.create.list" max-height="490" style="width: 100%;margin-bottom: 10px;">
		    <el-table-column sortable prop="shopName" label="小店"></el-table-column>
	      <el-table-column label="小店负责人">
		      <template slot-scope="scope">
		      	<el-input v-model="scope.row.chargeName"></el-input>
		      </template>
		    </el-table-column>
		  </el-table>
		    <div class="text-center">
		    	<el-button type="primary" icon="el-icon-star-on" v-if="!pageData.addDialog.isUpdate" @click.stop="addEvt">新增</el-button>
		    	<el-button type="primary" icon="el-icon-star-on" v-if="pageData.addDialog.isUpdate" @click.stop="updateEvt">修改</el-button>
		    	<el-button icon="el-icon-circle-close-outline" @click.stop="clearEvt">取消</el-button>
		    </div>
		</el-dialog>
	</el-main>
</template>

<script>
export default {
    data() {
      return {
      	options:[],
      	addDialogDisabled:false,
      	pageData:{
      		statusList:[ // 状态
	      		{name:"全部",value:"-1"},
	      		{name:"启用",value:"1"},
	      		{name:"禁用",value:"0"}
      		],
      		shopTypeList:[], // 小店类型
      		tableObj:{
      			siteNo :"", //  门店编号
				shopType :"", //  小店-类型 
				status :"-1", //  状态
				mandt:"", // 大区号
      			currentPage: 1,
      			pageSize: 10,
      			totalPage: 1, // 多少页
      			totalRows: 1, // 总数
      			pageArray:[10,20,30,40,50],
      			list: []
      		},
      		addDialog:{
      			tableHeight:0,
      			isUpdate:false,
      			dialogFormVisible:false,
      			create:{
      				siteNo:"",
      				shopType:"",
      				list:[]
      			},
      			formLabelWidth:'120px',
      		}
      	}
      }
    },
    created(){
			this.commonInfo()
			let height = document.documentElement.clientHeight
			this.pageData.addDialog.tableHeight = height*0.4
    },
    computed:{
    	
    },
    methods: {
    	initFunc(){
    		this.shopTypeListEvt()
    	},
    	commonInfo(){
				const loading = this.$loading({
	        lock: true,
	        spinner: 'wm-icon-loading',
	        background: 'rgba(0, 0, 0, 0.7)'
	      })
				let obj ={}
	  		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
	      	method:'post',
	      	headers:{
	    			"Content-type":"application/json",
	    		},
    			credentials: "include",
	      	body:JSON.stringify(obj)
	      })
	      .then(res=>res.json())
	      .then((res)=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	if(res.success){
	      		this.$store.dispatch("setMandt",res.model.mandt)
	      		this.$store.dispatch("setIndu",res.model.indu)
	      		this.$store.dispatch("setSiteNo",res.model.siteNo)
	      		this.pageData.tableObj.mandt = this.$store.getters.mandt
	      		this.pageData.addDialog.create.siteNo = this.$store.getters.siteNo
	      		this.pageData.tableObj.siteNo = this.$store.getters.siteNo
	      		if(res.model.siteNo!=""){
	      			this.addDialogDisabled = true	
	      		}
	      		this.initFunc()
	      	}else{
	      		this.$message.error(res.message)
	      	}
	      }).catch(err=>{
	      	setTimeout(() => {
	          loading.close();
	        }, 500)
	      	this.$message.error("错误："+err)
	      })
		
			},
    	addEvt(){
    		if(this.pageData.addDialog.create.siteNo == ""){
				this.$message({
		          message: '请输入门店编号!',
		          type: 'warning'
		        })
		        return false
			}
      	for(var i = 0;i<this.pageData.addDialog.create.list.length;i++){
					if(this.pageData.addDialog.create.list[i].chargeName == ""){
						this.$message({
		          message: '请输入小店负责人!',
		          type: 'warning'
		        })
		        return false
					}
				}
      	
    		var obj = this.pageData.addDialog.create.list
    		
    		fetch(this.$api.urls.POST_PARTNER_CREATE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
    			credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	if(res.success){
        		this.pageData.addDialog.dialogFormVisible = false
        		this.$alert('新增小店维护成功', '提示', {
		          confirmButtonText: '确定',
		          type: 'success',
		          center: true,
		          callback: action => {
				        this.queryEvt()  	
				        this.clearEvt()
		          }
		        })
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	this.$message.error("系统错误："+err)
        })
    		
    	},
    	updateEvt(){
    		var obj = this.pageData.addDialog.create.list
    		fetch(this.$api.urls.POST_PARTNER_UPDATE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	if(res.success){
        		this.pageData.addDialog.dialogFormVisible = false
        		this.$alert('修改小店维护成功', '提示', {
		          confirmButtonText: '确定',
		          type: 'success',
		          center: true,
		          callback: action => {
				        this.queryEvt()  	
				        this.clearEvt()
		          }
		        })
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	this.$message.error("系统错误："+err)
        })
    		
    	},
    	clearEvt(){
    		this.pageData.addDialog.create = {
  				siteNo:this.$store.getters.siteNo,
  				shopType:"",
  				list:[]
				}
    		this.pageData.addDialog.dialogFormVisible = false
    		this.pageData.addDialog.isUpdate = false
    	},
    	shopTypeEvt(index,item){
    		var obj ={
    			shopNo : item.row.shopNo, //  小店编号
					mandt : item.row.mandt, //  大区号
					indu : item.row.indu, //  业态
					shopType :item.row.shopType //  小店类型
    		}
    		fetch(this.$api.urls.POST_PARTNER_QUERYSHOPTYPE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	if(res.success){
        		this.$message({
		          message: res.message,
		          type: 'success'
		        })
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	this.$message.error("系统错误："+err)
        })
    	},
    	showUpdate(index,item){
    		this.pageData.addDialog.create.siteNo = item.row.siteNo, // 门店编号
				this.pageData.addDialog.create.shopType = item.row.shopType //  小店类型
				this.pageData.addDialog.dialogFormVisible = true
				this.pageData.addDialog.isUpdate = true
    		this.siteNoEvt()
    	},
    	statusEvt(index,item){
    		const loading = this.$loading({
          lock: true,
          //text: '疯狂加载中…',
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
    		var obj =[{
					mandt :item.row.mandt, //  大区号
					indu :item.row.indu, //  业态
					siteNo :item.row.siteNo, //  门店号
					shopType :item.row.shopType, //  小店类型
					shopNo :item.row.shopNo, //  小店编号
					status :item.row.status==1?0:1, //  状态
					creator :item.row.creator //  admin默认
    		}]
    		fetch(this.$api.urls.POST_PARTNER_UPDATE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	if(res.success){
        		this.$alert('修改状态成功', '提示', {
		          confirmButtonText: '确定',
		          type: 'success',
		          center: true,
		          callback: action => {
				        this.queryEvt()  	
		          }
		        })
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
    	},
    	siteNoEvt(){
    		if(this.pageData.addDialog.create.siteNo == ""){
				this.$message({
		          message: '请输入门店编号!',
		          type: 'warning'
		        })
		        return false
			}
		const loading = this.$loading({
          lock: true,
          //text: '疯狂加载中…',
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
    		let obj = {
      		siteNo:this.pageData.addDialog.create.siteNo, // 门店编号
      		mandt :this.$store.getters.mandt, //  大区号 300
					indu :this.$store.getters.indu, //  业态 W030
					shopType :this.pageData.addDialog.create.shopType //  小店类型
      	}
        fetch(this.$api.urls.POST_PARTNER_QUERYSHOP,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	// 组数据
	        	this.pageData.addDialog.create.list = []
	        	for(let i=0;i<res.model.length;i++){
	        		
	        		this.pageData.addDialog.create.list.push({
	        			siteNo :this.pageData.addDialog.create.siteNo, // 门店编号
								shopType :this.pageData.addDialog.create.shopType, // 小店类型
								chargeNo :res.model[i].chargeNo, // 小店负责人-员工编号
								chargeName :this.pageData.addDialog.isUpdate?res.model[i].chargeName:"", // 小店负责人-名称
								shopNo:res.model[i].shopNo, // 小店名称
								shopName:res.model[i].shopName, // 小店名称
								status :res.model[i].status, // 启用状态默认：1
								creator :res.model[i].creator, // 默认admin
								indu :res.model[i].indu, // 业态
								mandt :res.model[i].mandt // 大区号
							})
	        	}
	        	
	        }else{
	        	this.pageData.addDialog.create = {
		  				siteNo:this.$store.getters.siteNo,
		  				shopType:"",
		  				list:[]
						}
		    		this.pageData.addDialog.dialogFormVisible = true
		    		this.pageData.addDialog.isUpdate = false
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
    	},
    	shopTypeListEvt(){
    		var obj  = {
    			mandt:this.pageData.tableObj.mandt,
    			siteNo:this.pageData.tableObj.siteNo
    		}
    		fetch(this.$api.urls.POST_PARTNER_QUERYSHOPTYPE,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	if(res.success){
        		this.pageData.shopTypeList = res.model
        		this.queryEvt()
        	}else{
        		this.$message.error(res.message)
        	}
        }).catch(err=>{
        	this.$message.error("系统错误："+err)
        })
    	},
      queryEvt() {
      	const loading = this.$loading({
          lock: true,
          //text: '疯狂加载中…',
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
      	let obj = {
      		siteNo :this.pageData.tableObj.siteNo, //  门店编号 无
					shopType :this.pageData.tableObj.shopType, //  小店-类型 无
					status :this.pageData.tableObj.status, //  状态 无
					mandt :this.pageData.tableObj.mandt, // 大区号
					currentPage :this.pageData.tableObj.currentPage, 
					pageSize :this.pageData.tableObj.pageSize 
      	}
      	
        fetch(this.$api.urls.POST_PARTNER_QUERYSITESHOP,{
        	method:'post',
        	headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
        	body:JSON.stringify(obj)
        })
        .then(res=>res.json())
        .then(res=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
	        if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 页码当前
      			this.pageData.tableObj.pageSize = res.model.pageSize // 单页行数
      			this.pageData.tableObj.totalPage = res.model.pageCount // 多少页
      			this.pageData.tableObj.totalRows = res.model.count // 总数
      			const data =res.model.data
	        	for(let i =0;i<data.length;i++){
	        		data[i].created = this.$api.dateFormat("yyyy-MM-dd",data[i].created)
	        		for(let j=0;j<this.pageData.statusList.length;j++){
	        			if(data[i].status == this.pageData.statusList[j].value){
	        				data[i].statusName = this.pageData.statusList[j].name
	        				break
	        			}
	        		}
	        		for(let j=0;j<this.pageData.shopTypeList.length;j++){
	        			if(data[i].shopType == this.pageData.shopTypeList[j].shopType){
	        				data[i].induName = this.pageData.shopTypeList[j].induName
	        				break
	        			}
	        		}
	        	}
	        	this.pageData.tableObj.list = data
	        }else{
	        	this.$message.error(res.message)
	        }
        }).catch(err=>{
        	setTimeout(() => {
	          loading.close();
	        }, 500)
        	this.$message.error("系统错误："+err)
        })
        
      },
      handleSizeChange(val) {
        this.pageData.tableObj.pageSize = val // 单页行数
        this.queryEvt()
      },
      handleCurrentChange(val) {
        this.pageData.tableObj.currentPage = val // 当前页
        this.queryEvt()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



